<script>
export default {
  name: 'HelloWorld',
  props: {
    
  },
  data() {
    return {
      game: {
        state: 'playing',
        winner: null,
        characters: {
          1: {
            points:0,
            name: 'Tannkell',
            css: 'left:0%',
            img: '/images/monsters/1.png',
            sound: {
              move: 'sounds/move.mp3',
              winning: 'sounds/winning1.mp3'
            }
          },
          2: {
            points:0,
            name: 'Bob',
            css: 'left:0%',
            img: '/images/monsters/2.png',
            sound: {
              move: 'sounds/move2.mp3',
              winning: 'sounds/winning2.mp3'
            }
          },
          3: {
            points:0,
            name: 'Dill Doe',
            css: 'left:0%',
            img: '/images/monsters/3.png',
            sound: {
              move: 'sounds/move3.mp3',
              winning: 'sounds/winning3.mp3'
            }
          },
          4: {
            points:0,
            name: 'Kaktull',
            css: 'left:0%',
            img: '/images/monsters/4.png',
            sound: {
              move: 'sounds/move4.mp3',
              winning: 'sounds/winning4.mp3'
            }
          },
          5: {
            points:0,
            name: 'BRB',
            css: 'left:0%',
            img: '/images/monsters/5.png',
            sound: {
              move: 'sounds/move5.mp3',
              winning: 'sounds/winning5.mp3'
            }
          }
        },
      }
    }
  },
  methods: {
    draw() {
      let character = this.pickCharacter();
      this.updateScore(this.game.characters[character]);
    },
    move(character) {
      var audio = new Audio(character.sound.move); // path to file
      let distance = character.points * 100;
      let css = 'left:'+distance+'%';
      character.css = css;
      audio.play();
    },
    updateScore(character) {
      character.points++;
      this.move(character);
      if(character.points === 7){
         var winning = new Audio(character.sound.winning); // path to file
        this.game.state = 'winner';
        this.game.winner = character;
        winning.play();
      }
    },
    pickCharacter() {
      return Math.floor(Math.random() * (5 - 1 + 1)) + 1;
    },
  }
}
</script>

